import React from 'react';
import { cn } from '../lib/utils';
import { Button } from "./ui/button";

export const TagsRail = ({ notes, activeNoteId, tags = [], onTagClick }) => {
    // Get active tags for the current note
    const activeTags = React.useMemo(() => {
        if (!activeNoteId) return new Set();
        const note = notes.find(n => n.id === activeNoteId);
        if (!note) return new Set();

        // Extract tags from note content (naive regex or pre-parsed?)
        // Assuming tags are hashtags like #tag
        const found = note.content.match(/#[\w-]+/g) || [];
        // Clean them (remove #)
        return new Set(found.map(t => t.slice(1)));
    }, [activeNoteId, notes]);

    // Sort tags alphabetically? Or by frequency?
    // Let's stick to alphabetical for stability.
    const sortedTags = React.useMemo(() => {
        return [...tags].sort();
    }, [tags]);

    // Sliding Window Logic for Tags
    const windowedTags = React.useMemo(() => {
        if (!activeTags.size || tags.length <= 25) return sortedTags;

        // Find the index of the first active tag in the sorted list
        // This acts as our "center of gravity"
        const firstActiveTag = sortedTags.find(t => activeTags.has(t));
        const activeIndex = sortedTags.indexOf(firstActiveTag);

        if (activeIndex === -1) return sortedTags.slice(0, 25);

        // Center (~12th item)
        let start = activeIndex - 12;
        let end = activeIndex + 13;

        // Clamp
        if (start < 0) {
            start = 0;
            end = 25;
        }
        if (end > sortedTags.length) {
            end = sortedTags.length;
            start = Math.max(0, end - 25);
        }

        return sortedTags.slice(start, end);
    }, [sortedTags, activeTags, tags.length]);

    if (!tags || tags.length === 0) return null;

    return (
        <div className="fixed right-8 top-24 bottom-32 w-24 z-40 hidden sm:flex flex-col justify-center pointer-events-none">
            {/* Inner container with scroll if needed */}
            <div className="max-h-full overflow-y-auto no-scrollbar py-4 flex flex-col items-center">
                <div className="flex flex-col gap-1 w-full px-2">
                    {windowedTags.map(tag => {
                        const isActive = activeTags.has(tag);
                        const colorClass = getTagColor(tag);

                        // Extract just the text color from the composite class for the label hover
                        // Or just apply the full bg to the label? 
                        // Let's apply specific styles: 
                        // Tick: Always takes the color (background). 
                        // Label: Takes the color on hover/active.

                        // We need to parse the bg color for the tick. 
                        // But wait, the class string is "bg-yellow-200/50 text-yellow-900".
                        // Use it directly for a small badge? 
                        // User wants "Physical Highlighter".

                        return (
                            <div
                                key={tag}
                                onClick={() => onTagClick(tag)}
                                className="group flex items-center justify-end w-full gap-3 h-3 cursor-pointer pointer-events-auto"
                            >
                                {/* Tag Label (Appears on LEFT of tick for Right Rail) */}
                                <span className={cn(
                                    "text-[11px] font-mono tracking-wide transition-all duration-300 whitespace-nowrap px-1.5 py-0.5 rounded",
                                    isActive
                                        ? `opacity-100 translate-x-0 ${colorClass}`
                                        : "opacity-0 text-muted-foreground translate-x-4 group-hover:opacity-100 group-hover:translate-x-0"
                                )}>
                                    #{tag}
                                </span>

                                {/* Ruler Tick (Right side) */}
                                {/* We can't easily extract just the 'bg-color' bit from the utility string for the tick 
                                    if it's a composite string. 
                                    Simple solution: Use the text color for the tick background? 
                                    Or just rely on 'bg-foreground' for simplicity and keep color only on the label?
                                    User said "Highlighter marker".
                                    Let's keep the tick black (zinc) for structure, and color only the label. 
                                    OR color the tick too. Let's try coloring the tick with 'currentColor' by setting text color on parent? 
                                */}
                                <div className={cn(
                                    "h-[1.5px] rounded-full transition-all duration-300 group-hover:w-6 group-hover:opacity-100",
                                    isActive
                                        ? "w-6 bg-foreground opacity-100" // Keep tick neutral/strong
                                        : "w-2 bg-muted-foreground opacity-30"
                                )} />
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};
